@import url(https://fonts.googleapis.com/css2?family=Risque&display=swap);
body{margin:0;font-family:'Risque', cursive;box-sizing:border-box}

.page{min-height:100vh;position:relative}.page__main{padding:0 0 80px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background:#d5feff;background:radial-gradient(circle, #d5feff 4%, #f5eefc 100%)}@media (max-width: 768px){.page__main{-webkit-flex-direction:column;flex-direction:column}}.page__sort{padding:20px;position:fixed;top:80px;left:0}@media (max-width: 760px){.page__sort{position:static}}

.footer{position:absolute;bottom:0;left:0;right:0;width:100%;height:80px;border-top:1px solid #363131;background:#fffe00;background:radial-gradient(circle, #fffe00 45%, #7500ff 100%);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.footer__link{text-decoration:none;color:darkblue}.footer__link:hover{cursor:pointer;text-decoration:underline}

.header{width:100%;height:80px;background:#ff0;background:linear-gradient(109deg, #ff0 0%, #6767cb 100%);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;text-transform:uppercase;color:#d5feff;font-weight:bold;font-size:40px;letter-spacing:3px}

.loader{height:95vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:100%;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;letter-spacing:2px}@media (max-width: 760px){.loader{-webkit-align-items:flex-start;align-items:flex-start}}.loader__paw{display:block;width:200px;height:200px;-webkit-animation:paw_rotate 6s linear infinite;animation:paw_rotate 6s linear infinite}@-webkit-keyframes paw_rotate{0%{-webkit-transform:rotate(0);transform:rotate(0)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes paw_rotate{0%{-webkit-transform:rotate(0);transform:rotate(0)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.sort{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.sort__title{margin:0}.pagination__container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;list-style:none;padding:0}.pagination__page{border:1px solid #dcdcdc;border-radius:6px;margin-right:10px;width:50px;height:40px;cursor:pointer}.pagination__page a{display:block;padding:10px;text-align:center}.pagination__disabled{cursor:not-allowed}.pagination__active{border:2px solid #000;font-weight:bold}.pagination__link{display:block;padding:10px;text-align:center}.pagination__break{padding:10px}.previous{border:1px solid #dcdcdc;border-radius:6px;margin-right:10px;width:70px;height:40px;cursor:pointer}.previous a{display:block;padding:10px;text-align:center}.next{border:1px solid #dcdcdc;border-radius:6px;margin-right:10px;cursor:pointer;width:70px;height:40px}.next a{display:block;padding:10px;text-align:center}

.card-list{margin:0 auto;-webkit-justify-content:flex-start;justify-content:flex-start;display:-webkit-flex;display:flex;grid-gap:40px;gap:40px;padding:40px 20px 40px 200px;max-width:1400px;-webkit-flex-wrap:wrap;flex-wrap:wrap}@media (max-width: 760px){.card-list{grid-gap:20px;gap:20px;padding:20px;-webkit-justify-content:center;justify-content:center}}.card{width:250px;border:1px solid #636161;background:#ff0;background:radial-gradient(circle, #ff0 0%, #6767cb 100%);border-radius:5px;-webkit-flex-shrink:0;flex-shrink:0;letter-spacing:2px;color:white;transition:border-radius 0.3s linear, -webkit-transform 0.3s linear;transition:transform 0.3s linear, border-radius 0.3s linear;transition:transform 0.3s linear, border-radius 0.3s linear, -webkit-transform 0.3s linear}.card:hover{-webkit-transform:scale(1.2);transform:scale(1.2);border-radius:30px;cursor:pointer}.card__img{position:relative}.card__name{margin:0;padding:5px;text-align:center}.card__available,.card__price,.card__category{text-align:center;padding:3px}.card__price{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;color:red;font-weight:bold;width:100px;height:100px;background-image:url(../../static/media/Price-Tag-PNG-Image.ea67bbaa.png);background-position:center;background-size:contain;background-repeat:no-repeat;position:absolute;top:0;right:0}

